import React, { useEffect, useState } from 'react';
import useStyle from "./styles.js"
import { Paper, Typography, Button, TextField } from '@material-ui/core';
import FileBase from 'react-file-base64';
import {useDispatch} from 'react-redux'
import { createPost, updatePost} from '../../actions/posts.js';
import { useSelector } from 'react-redux';

const Form = ({currentId,setCurrentId}) => {
    const [postData, setPostData] = useState({
        creator: '',
        title: '',
        message: '',
        tags: '',
        selectedFile: ''
    });

    const formPost = useSelector((state) => ((currentId)? state.posts.find(post => post._id===currentId):null));
    useEffect(()=>{
        if(formPost)
        setPostData(formPost);
    },[formPost]);

    const classes = useStyle();
    const dispatch=useDispatch();

    const clear = () =>{
        setCurrentId(null);
        setPostData({
            creator: '',
            title: '',
            message: '',
            tags: '',
            selectedFile: ''
        });
    }
    
    const handleSubmit = (e) => {
        e.preventDefault(); 
        if(currentId)
            dispatch(updatePost(currentId,postData));
        else
            dispatch(createPost(postData));
        clear();
    }
    return (
        <Paper className={classes.paper}>
            <form autoComplete='off' noValidate className={`${classes.root} ${classes.form}`} onSubmit={handleSubmit}>
                <Typography variant='h6'>{!currentId? "Creating a Memory":"Editing a Memory"}</Typography>
                <TextField
                    name="creator" variant='outlined' label="Creator" fullWidth value={postData.creator} onChange={(e) => {
                        // var tempPostData=postData;
                        // tempPostData.creator=e.target.value;
                        // setPostData(tempPostData);
                        setPostData({ ...postData, creator: e.target.value })
                    }}
                />
                <TextField
                    name="title" variant='outlined' label="title" fullWidth value={postData.title} onChange={(e) => {
                        setPostData({ ...postData, title: e.target.value })
                    }}
                />
                <TextField
                    name="message" variant='outlined' label="message" fullWidth value={postData.message} onChange={(e) => {
                        setPostData({ ...postData, message: e.target.value })
                    }}
                />
                <TextField
                    name="tags" variant='outlined' label="tags" fullWidth value={postData.tags} onChange={(e) => {
                        setPostData({ ...postData, tags: e.target.value.split(',')})
                    }}
                />
                <div className={classes.fileInput}> <FileBase type="file" multiple = {false} onDone = {({base64}) => setPostData({...postData,selectedFile:base64})} /> </div>
                <Button className={classes.buttonSubmit} variant="contained" color='primary' size="large" type="submit" fullWidth>Submit</Button>
                <Button variant='contained' color="secondary" size='small'onClick={clear} fullWidth>Clear</Button>
            </form>
        </Paper>
    )
}

export default Form;